import Layout from '../common/Layout';
import axios from 'axios';
import { useState, useEffect, useRef } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faYoutube } from '@fortawesome/free-brands-svg-icons';

import Pop from '../common/Pop';

function Youtube() {
	const icon = useRef(null);
	//  
	const [Vids, setVids] = useState([]);

	// 
	const pop = useRef(null);

	//   
	const [Index, setIndex] = useState(0);

	const getYoutube = async () => {
		//   return    (mount)   
		// icon       
		// -->   Vids  state    Vids  article  
		//      article   
		//          useEffect    
		//        article   .
		//     useEffect    
		const key = 'AIzaSyD1ZRgZNZXs590CNC6IbqqDi5RFFZNf1VM';
		const playlist = 'PL4lFp_wxDge_0KaYa0XNk7f4CZEA4Z1J3';
		const num = 6;
		const url = `https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&key=${key}&playlistId=${playlist}&maxResults=${num}`;

		await axios.get(url).then((json) => {
			console.log(json.data.items);
			setVids(json.data.items);
		});
	};

	//       
	//      (mount)   

	useEffect(getYoutube, []);

	//  svg 
	// ->   Vids     useRef   
	//    state  useEffect  
	//   Vids    useEffect axios   Vids    article    
	// article         useRef icon  
	useEffect(() => {
		console.log(icon);
	}, [Vids]);

	//  
	//   useRef     useEffect 
	//               useEffect  

	return (
		<>
			<Layout name={'Youtube'}>
				{Vids.map((vid, idx) => (
					//    id  idx   
					<article key={vid.id}>
						<h2>
							{vid.snippet.title.length > 30
								? vid.snippet.title.substr(0, 30) + '...'
								: vid.snippet.title}
						</h2>
						<div className='txt'>
							<p>
								{vid.snippet.description.length > 200
									? vid.snippet.description.substr(0, 200) + '...'
									: vid.snippet.description}
							</p>
							<span>{vid.snippet.publishedAt.split('T')[0]}</span>
						</div>
						<div className='pic'>
							<FontAwesomeIcon
								icon={faYoutube}
								ref={icon}
								onClick={() => {
									pop.current.open();
									//    
									setIndex(idx);
								}}
							/>
							<img
								src={vid.snippet.thumbnails.maxres.url}
								alt={vid.snippet.title}
							/>
						</div>
					</article>
				))}
			</Layout>

			<Pop ref={pop}>
				{Vids.length !== 0 && (
					<iframe
						src={`https://www.youtube.com/embed/${Vids[Index].snippet.resourceId.videoId}`}
						frameBorder='0'
						allowFullScreen></iframe>
				)}
			</Pop>
		</>
	);
}

export default Youtube;
